

'use strict';
import React, {Component} from 'react';
import {
    View
} from 'react-native';

export default class ViewPort extends Component {
    constructor(props) {

        super(props);


    }

    render(){

        return (
            <View id="main" {...this.props} style={Object.assign({...this.props.style||{}},{flex:1,height:'100%'})}>

            </View>
        )
    }
}

